import { Button, TextField, Typography } from '@mui/material';
import React from 'react';
import { IPropsRegister } from '../../../common/types/auth';

const RegisterPage: React.FC<IPropsRegister> = (props: IPropsRegister): JSX.Element => {
    const { setEmail, setFirstName, setPassword, setRepeatPassword, setUsername, navigate } = props
    return (
        <>
            <div className='register_p0'>
                <div className='register_p1'>
                    <Typography color={'#50735F'} variant="h2" fontFamily='Alice' textAlign={'center'}>  </Typography>
                    <Typography color={'#50735F'} variant="body1" marginBottom={3} fontFamily='Alice' textAlign={'center'}>     </Typography>
                    <TextField fullWidth={true} margin='normal' label="" variant="outlined" placeholder='  ' onChange={(e) => setFirstName(e.target.value)} />
                    <TextField fullWidth={true} margin='normal' label="" variant="outlined" placeholder='  ' onChange={(e) => setUsername(e.target.value)} />
                    <TextField fullWidth={true} margin='normal' label="E-mail" variant="outlined" placeholder='  email' onChange={(e) => setEmail(e.target.value)} />
                </div>
                <div className='register_p2'>
                    <TextField type='password' fullWidth={true} margin='normal' label="" variant="outlined" placeholder='  ' onChange={(e) => setPassword(e.target.value)} />
                    <TextField type='password' fullWidth={true} margin='normal' label=" " variant="outlined" placeholder='  ' onChange={(e) => setRepeatPassword(e.target.value)} />
                </div>
            </div>
            <div className='register_p3'>
                <Button type='submit' sx={{ "&:hover": {backgroundColor: '#50735F'}, backgroundColor: '#50735F', fontFamily: 'Forum', marginTop: 2, marginBottom: 2, width: '60%' }} variant="contained"></Button>
                <Typography variant="body1" sx={{ fontFamily: 'Alice', }}>     ? <span className='incitingText' onClick={() => navigate('/login')} ></span> </Typography>
            </div>
        </>
    );
};

export default RegisterPage;